import './style.scss';
import PropTypes from 'prop-types';
import {ReactSortable} from "react-sortablejs";
import ItemComponent from './ItemComponent';
import {useEffect, useState} from 'react';

const {__} = wp.i18n;
const { Button, Dashicon, SelectControl} = wp.components;

const SocialComponent = props => {

	let value = props.control.setting.get();
	let baseDefault = {
		'items': [
			{
				'id': 'facebook',
				'enabled': true,
				'url': '',
			},
			{
				'id': 'twitter',
				'enabled': true,
				'url': '',
			}
		],
	};
	let defaultValue = props.control.params.default ? {
		...baseDefault,
		...props.control.params.default
	} : baseDefault;

	value = value ? {
		...defaultValue,
		...value
	} : defaultValue;

	let defaultParams = {
		'group': 'social_item_group',
		'options': [
			{value: 'facebook', label: __('Facebook', 'aeonwp')},
			{value: 'twitter', label: __('Twitter', 'aeonwp')},
			{value: 'instagram', label: __('Instagram', 'aeonwp')},
			{value: 'youtube', label: __('YouTube', 'aeonwp')},
			{value: 'facebook_group', label: __('Facebook Group', 'aeonwp')},
			{value: 'vimeo', label: __('Vimeo', 'aeonwp')},
			{value: 'pinterest', label: __('Pinterest', 'aeonwp')},
			{value: 'linkedin', label: __('Linkedin', 'aeonwp')},
			{value: 'medium', label: __('Medium', 'aeonwp')},
			{value: 'wordpress', label: __('WordPress', 'aeonwp')},
			{value: 'reddit', label: __('Reddit', 'aeonwp')},
			{value: 'patreon', label: __('Patreon', 'aeonwp')},
			{value: 'github', label: __('GitHub', 'aeonwp')},
			{value: 'dribbble', label: __('Dribbble', 'aeonwp')},
			{value: 'behance', label: __('Behance', 'aeonwp')},
			{value: 'vk', label: __('VK', 'aeonwp')},
			{value: 'xing', label: __('Xing', 'aeonwp')},
			{value: 'rss', label: __('RSS', 'aeonwp')},
			{value: 'email', label: __('Email', 'aeonwp')},
			{value: 'phone', label: __('Phone', 'aeonwp')},
			{value: 'whatsapp', label: __('WhatsApp', 'aeonwp')},
			{value: 'google_reviews', label: __('Google Reviews', 'aeonwp')},
			{value: 'google_play', label: __('Google Play', 'aeonwp')},
			{value: 'telegram', label: __('Telegram', 'aeonwp')},
			{value: 'yelp', label: __('Yelp', 'aeonwp')},
			{value: 'twitch', label: __('Twitch', 'aeonwp')},
			{value: 'tiktok', label: __('TikTok', 'aeonwp')},
			{value: 'trip_advisor', label: __('Tripadvisor', 'aeonwp')},
			{value: 'discord', label: __('Discord', 'aeonwp')},
			{value: 'flickr', label: __('Flickr', 'aeonwp')},
			{value: 'slack', label: __('Slack', 'aeonwp')},
			{value: 'weibo', label: __('Weibo', 'aeonwp')},
			{value: 'kakaotalk', label: __('KakaoTalk', 'aeonwp')},
			{value: 'kakaostory', label: __('KakaoStory', 'aeonwp')},
		].sort((a, b) => {
			if (a.value < b.value) {
				return -1;
			}
			if (a.value > b.value) {
				return 1;
			}
			return 0;
		})
	};

	let controlParams = props.control.params.input_attrs ? {
		...defaultParams,
		...props.control.params.input_attrs,
	} : defaultParams;

	let availibleSocialOptions = [];
	controlParams.options.map((option) => {
		if (!value.items.some(obj => obj.id === option.value)) {
			availibleSocialOptions.push(option);
		}
	});

	const [state, setState] = useState({
		value: value,
		isVisible: false,
		control: (undefined !== availibleSocialOptions[0] && undefined !== availibleSocialOptions[0].value ? availibleSocialOptions[0].value : '')
	});

	useEffect( () => {
		// If settings are changed externally.
		setState(prevState => ({
			...prevState,
			value: props.control.setting.get()
		}));


	}, [props]);

	const updateValues = (value) => {
		props.control.setting.set({
			...props.control.setting.get(),
			...value,
			flag: !props.control.setting.get().flag
		});
	};

	const onDragStop = () => {
		let dropzones = document.querySelectorAll('.aeon-builder-area');
		let i;

		for (i = 0; i < dropzones.length; ++i) {
			dropzones[i].classList.remove('aeon-dragging-dropzones');
		}
	};

	const saveArrayUpdate = (value, index) => {
		let updateState = state.value;
		let items = updateState.items;
		const newItems = items.map((item, thisIndex) => {

			if (index === thisIndex) {
				item = {
					...item,
					...value
				};
			}
			return item;
		});

		updateState.items = newItems;
		setState(prevState => ({
			...prevState,
			value: updateState
		}));
		updateValues(updateState);
	};

	const toggleEnableItem = (value, itemIndex) => {
		saveArrayUpdate({
			enabled: value
		}, itemIndex);
	};

	const onChangeURL = (value, itemIndex) => {
		saveArrayUpdate({
			url: value
		}, itemIndex);
	};

	const removeItem = (itemIndex) => {

		let updateState = state.value;
		let update = updateState.items;
		let updateItems = [];
		{
			update.length > 0 && update.map((old, index) => {
				if (itemIndex !== index) {
					updateItems.push(old);
				}
			});
		}
		updateState.items = updateItems;
		setState(prevState => ({
			...prevState,
			value: updateState
		}));
		updateValues(updateState);
	};

	const addItem = () => {
		const itemControl = state.control;
		setState(prevState => ({
			...prevState,
			isVisible: false
		}));

		if (itemControl) {
			let updateState = state.value;
			let update = updateState.items;

			const itemLabel = controlParams.options.filter(function (o) {
				return o.value === itemControl;
			});
			let newItem = {
				'id': itemControl,
				'enabled': true,
				'url': '',
				'label': itemLabel[0].label
			};
			update.push(newItem);
			updateState.items = update;
			let availibleSocialOptions = [];
			controlParams.options.map(option => {
				if (!update.some(obj => obj.id === option.value)) {
					availibleSocialOptions.push(option);
				}
			});

			setState(prevState => ({
				...prevState,
				control: (undefined !== availibleSocialOptions[0] && undefined !== availibleSocialOptions[0].value ? availibleSocialOptions[0].value : '')
			}));
			setState(prevState => ({
				...prevState,
				value: updateState
			}));

			updateValues(updateState);
		}
	};

	const onDragEnd = (items) => {
		let updateState = state.value;
		let update = updateState.items;
		let updateItems = [];
		{
			items.length > 0 && items.map(item => {
				update.filter(obj => {
					if (obj.id === item.id) {
						updateItems.push(obj);
					}
				});
			});
		};

		if (!arraysEqual(update, updateItems)) {
			update.items = updateItems;
			updateState.items = updateItems;
			setState(prevState => ({
				...prevState,
				value: updateState
			}));
			updateValues(updateState);
		}
	};

	const arraysEqual = (a, b) => {
		if (a === b) return true;
		if (a == null || b == null) return false;
		if (a.length != b.length) return false;
		for (let i = 0; i < a.length; ++i) {
			if (a[i] !== b[i]) return false;
		}
		return true;
	};

	const currentList = typeof state.value != "undefined" && state.value.items != null && state.value.items.length != null && state.value.items.length > 0 ? state.value.items : [];
	let theItems = [];
	{
		currentList.length > 0 && currentList.map(item => {
			theItems.push({
				id: item.id
			});
		});
	};

	return <div className="aeon-control-field aeon-sorter-items">
		<div className="aeon-sorter-row">
			<ReactSortable animation={100} onStart={() => onDragStop()} onEnd={() => onDragStop()}
						   group={controlParams.group}
						   className={`aeon-sorter-drop aeon-sorter-sortable-panel aeon-sorter-drop-${controlParams.group}`}
						   handle={'.aeon-sorter-item-panel-header'} list={theItems}
						   setList={newState => onDragEnd(newState)}>
				{currentList.length > 0 && currentList.map((item, index) => {
					return <ItemComponent removeItem={remove => removeItem(remove)}
										  toggleEnabled={(enable, itemIndex) => toggleEnableItem(enable, itemIndex)}
										  onChangeURL={(url, itemIndex) => onChangeURL(url, itemIndex)}
										  key={item.id} index={index} item={item} controlParams={controlParams}/>;

				})}
			</ReactSortable>
		</div>
		{undefined !== availibleSocialOptions[0] && undefined !== availibleSocialOptions[0].value &&
		<div className="aeon-social-add-area">
			{<SelectControl value={state.control} options={availibleSocialOptions} onChange={value => {
				setState(prevState => ({
					...prevState,
					control: value
				}));
			}}/>}
			{<Button
				className="aeon-sorter-add-item"
				isPrimary
				onClick={() => {
					addItem();
				}}
			>
				<Dashicon icon="plus-alt2"/>
			</Button>}

		</div>}
	</div>;

};

SocialComponent.propTypes = {
	control: PropTypes.object.isRequired,
};

export default SocialComponent;
