import './style.scss';
import googleFonts from './google-fonts';
import Select from 'react-select';
import PropTypes from 'prop-types';
import {SelectControl} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import {useEffect, useState} from 'react';

const FontFamilyComponent = props => {
	const {
		label,
		link
	} = props.control.params;

	let labelHtml = null,
		inp_array = [];

	if (label) {
		labelHtml = <span className="customize-control-title">{label}</span>;
	}

	if (undefined !== link) {
		let splited_values = link.split(" ");
		splited_values.map((item, i) => {
			let item_values = item.split("=");

			if (undefined !== item_values[1]) {
				inp_array[item_values[0]] = item_values[1].replace(/"/g, "");
			}
		});
	}

	let value = props.control.settings.fontFamily.get();
	let valueWeight = props.control.settings.fontWeight ? props.control.settings.fontWeight.get() : 'normal';
	let valueTransform = props.control.settings.fontTransform ? props.control.settings.fontTransform.get() : 'none';
	const [state, setState] = useState(value);
	const [stateWeight, setStateWeight] = useState(valueWeight);
	const [stateTransform, setStateTransform] = useState(valueTransform);

	useEffect( () => {
		// If settings are changed externally.
		if( state !== value ) {
			setState( value );
		}
		if( stateWeight !== valueWeight ) {
			setStateWeight( valueWeight );
		}
		if( stateTransform !== valueTransform ) {
			setStateTransform( valueTransform );
		}
	}, [props]);

	const fonts = [
		{ value: '', label: __( 'Default', 'aeonwp' ) },
		{ value: 'Arial', label: 'Arial' },
		{ value: 'Helvetica', label: 'Helvetica' },
		{ value: 'Times New Roman', label: 'Times New Roman' },
		{ value: 'Georgia', label: 'Georgia' },
	];

	Object.keys( googleFonts ).forEach( ( k ) => {
		fonts.push(
			{ value: k, label: k, weight: googleFonts[ k ].weight }
		);
	} );

	let weightObj = [
		{ value: '', 		label: __( 'Default', 'aeonwp' ) },
		{ value: 'normal', 	label: __( 'Normal', 'aeonwp' ) },
		{ value: 'bold', 	label: __( 'Bold', 'aeonwp' ) },
		{ value: '100', 	label: '100' },
		{ value: '200', 	label: '200' },
		{ value: '300', 	label: '300' },
		{ value: '400', 	label: '400' },
		{ value: '500', 	label: '500' },
		{ value: '600', 	label: '600' },
		{ value: '700', 	label: '700' },
		{ value: '800', 	label: '800' },
		{ value: '900', 	label: '900' },
	];

	if ( 'undefined' !== typeof googleFonts[ state ] && 'undefined' !== typeof googleFonts[ state ].weight ) {
		weightObj = [
			{ value: '', label: __( 'Default', 'aeonwp' ) },
		];

		googleFonts[ state ].weight.filter( function( k ) {
			const hasLetters = k.match( /[a-z]/g );
			const hasNumbers = k.match( /[0-9]/g );

			if ( ( hasLetters && hasNumbers ) || 'italic' === k || 'regular' === k ) {
				return false;
			}

			return true;
		} ).forEach( ( k ) => {
			weightObj.push(
				{ value: k, label: k }
			);
		} );
	}

	return <>
		<label className="customize-control-title">
			{labelHtml}
		</label>
		<Select
			options={ fonts }
			value={ { value: state, label: state } }
			isMulti={ false }
			maxMenuHeight={ 300 }
			onChange={ ( value ) => {
				setState( value.label );
				props.control.settings.fontFamily.set( value.label );
			} }
			className="react-select-container"
			classNamePrefix="react-select"
		/>

		<div className="ae-typo-weight-transform">
			<SelectControl
				label={ __( 'Weight', 'aeonwp' ) }
				value={ stateWeight }
				options={ weightObj }
				onChange={ ( weight ) => {
					setStateWeight( weight );
					props.control.settings.fontWeight.set( weight );
				} }
				className="components-base-control"
			/>

			<SelectControl
				label={ __( 'Text Transform', 'aeonwp' ) }
				value={ stateTransform }
				options={ [
					{ value: 'none', label: __( 'None', 'aeonwp' ) },
					{ value: 'capitalize', label: __( 'Capitalize', 'aeonwp' ) },
					{ value: 'uppercase', label: __( 'Uppercase', 'aeonwp' ) },
					{ value: 'lowercase', label: __( 'Lowercase', 'aeonwp' ) },
				] }
				onChange={ ( transform ) => {
					setStateTransform( transform );
					props.control.settings.fontTransform.set( transform );
				} }
				className="components-base-control"
			/>
		</div>
	</>;
};

FontFamilyComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default FontFamilyComponent;
