import './style.scss';
import PropTypes from 'prop-types';

const SortableComponent = props => {
	let labelHtml = null;

	const {
		label,
		value,
		choices,
		inputAttrs
	} = props.control.params;

	if (label) {
		labelHtml = <span className="customize-control-title">{label}</span>;
	}

	let visibleMetaHtml = Object.values(value).map(choiceID => {
		let html = '';
		if (choices[choiceID]) {
			html = <div {...inputAttrs} key={choiceID} className='aeon-sortable-item' data-value={choiceID}>
				{choices[choiceID]}
				<i className="dashicons dashicons-visibility visibility"></i>
			</div>;
		}
		return html;
	});

	let invisibleMetaHtml = Object.keys(choices).map(choiceID => {
		let html = '';
		if (Array.isArray(value) && -1 === value.indexOf(choiceID)) {
			html = <div {...inputAttrs} key={choiceID} className='aeon-sortable-item invisible' data-value={choiceID}>
				{choices[choiceID]}
				<i className="dashicons dashicons-visibility visibility"></i>
			</div>;
		}
		return html;
	});

	return <label className='aeon-sortable'>
		{labelHtml}
		<div className="sortable">
			{visibleMetaHtml}
			{invisibleMetaHtml}
		</div>
	</label>;

};

SortableComponent.propTypes = {
	control: PropTypes.object.isRequired
};
export default React.memo( SortableComponent );
