import PropTypes from 'prop-types';
import {Fragment} from '@wordpress/element';

const ActionButtonComponent = props => {
	let htmlLabel = null;
	let htmlDescription = null;
	let htmlButton = null;

	if (props.control.params.label) {
		htmlLabel = <span className="customize-control-title">{props.control.params.label}</span>;
	}

	if (props.control.params.description) {
		htmlDescription = <span className="description customize-control-description">{props.control.params.description}</span>;
	}

	if (props.control.params.button) {
		htmlButton = <button type="button" className={`button button-secondary ${props.control.params.custom_class}`}>{props.control.params.button}</button>;
	}

	return <Fragment>
		{htmlLabel}
		{htmlDescription}
		{htmlButton}
	</Fragment>;
};
ActionButtonComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default React.memo( ActionButtonComponent );
