import './style.scss';
import PropTypes from 'prop-types';
import {Fragment} from '@wordpress/element';
import {useState} from 'react';

const RadioImageComponent = props => {
	const [props_value, setPropsValue] = useState(props.control.setting.get());

	const onLayoutChange = (value) => {
		setPropsValue(value);
		props.control.setting.set(value);
	};

	const {
		label,
		id,
		choices,
		choices_titles
	} = props.control.params;

	let htmlLabel = null,
		htmlRadio,
		inp_array = [];

	if (label) {
		htmlLabel = <span className="customize-control-title">{label}</span>;
	}

	htmlRadio = Object.entries(choices).map(([key, value]) => {
		let checked = props_value === key ? true : false;
		return <Fragment key={key}>
			<div>
				<input {...inp_array} className="image-select" type="radio" value={key} id={id + key} checked={checked} onChange={() => onLayoutChange(key)}/>
				<label htmlFor={id + key} className="aeon-radio-img-svg" title={choices_titles[key]}>
					<span dangerouslySetInnerHTML={{
						__html: choices[key]
					}}/>
				</label>
			</div>
		</Fragment>;
	});
	return <Fragment>
		<label className="customizer-text">
			{htmlLabel}
		</label>
		<div className="aeon-radio-images">
			{htmlRadio}
		</div>
	</Fragment>;
};

RadioImageComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default React.memo( RadioImageComponent );
