import classnames from 'classnames';

import {
	render,
} from '@wordpress/element';

export const WrapperControl = wp.customize.aeControl.extend( {
	renderContent: function() {
		const control = this;
		const choices = control.params.choices;

		const form = () => {
			return (
				<div
					className={ classnames( {
						'aeon-customize-control-wrapper': true,
						[ choices.class ]: !! choices.class,
					} ) }
					id={ choices.id }
					data-wrapper-type={ choices.type }
				>
					{ choices.items.map( ( wrapper ) => {
						return <div key={ wrapper } id={ wrapper + '--wrapper' }></div>;
					} ) }
				</div>
			);
		};

		render(
			form(),
			control.container[ 0 ]
		);
	}
} );