import './style.scss';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import {RangeControl,SelectControl,Dashicon} from '@wordpress/components';
import {useState,useEffect} from 'react';

const SliderComponent = props => {
	let prop_value = props.control.settings.size ? props.control.settings.size.get() : '',
		suffix_value = props.control.settings.sizeUnit ? props.control.settings.sizeUnit.get() : props.control.params.defaultUnit;

	const [state, setState] = useState( prop_value );
	const [stateSuffix, setStateSuffix] = useState( suffix_value );

	useEffect( () => {
		// If settings are changed externally.
		if ( state !== prop_value ) {
			setState( prop_value );
		}
		if ( stateSuffix !== suffix_value ) {
			setStateSuffix( suffix_value );
		}
	}, [props]);

	const resetButton = ( defaultVal, defaultValUnit ) => {
		return (
			<div className="aeon-slider-reset-wrap">
				<button
					className="aeon-reset"
					disabled={ JSON.stringify(state) === JSON.stringify(defaultVal)}
					onClick={ e => {
						e.preventDefault();
						props.control.settings.size.set(defaultVal);
						setState(defaultVal);
			
						if (suffix) {
							props.control.settings.sizeUnit.set(defaultValUnit);
							setStateSuffix(defaultValUnit);
						}
					} }
				>
				<Dashicon icon='image-rotate'/>
				</button>
			</div>
		);
	};

	const renderInputHtml = () => {
		const {
			input_attrs,
		} = props.control.params;

		let defaultVal = props.control.params.default;

		let minVal = 0,
			maxVal = 100,
			stepVal = 1;

		const defaults = { min: minVal, max: maxVal, step: stepVal };
		const controlProps = {
			...defaults,
			...( input_attrs || {} ),
		};
		const { min, max, step } = controlProps;

		if (suffix) {
			if ( 'px' === stateSuffix ) {
				minVal = min || 0;
				maxVal = max || 500;
				stepVal = step || 1;
			} else if ( '%' === stateSuffix ) {
				minVal = 0;
				maxVal = 100;
				stepVal = 1;
			} else if ( '-' === stateSuffix || 'em' === stateSuffix || 'rem' === stateSuffix || 'vh' === stateSuffix || 'vw' === stateSuffix ) {
				minVal = 0;
				maxVal = 20;
				stepVal = .1;
			}
		} else {
			minVal = min || minVal;
			maxVal = max || maxVal;
			stepVal = step || stepVal;
		}

		let savedValue = ( state || 0 === state ) ? parseFloat( state ) : '';

		if ( 1 === stepVal ) {
			savedValue = ( state || 0 === state ) ? parseInt( state ) : '';
		}

		return <RangeControl
			resetFallbackValue={ defaultVal }
			value={ savedValue }
			min={ minVal }
			max={ maxVal }
			step={ stepVal }
			onChange={ ( value ) => {
				setState( value );
				props.control.settings.size.set( value );
			} }
		/>;
	};

	const {
		label,
		suffix,
	} = props.control.params;

	let labelHtml = null,
		suffixHtml = null,
		defaultVal = props.control.params.default,
		defaultValUnit = props.control.params.defaultUnit;

	if (label) {
		labelHtml = <div className="aeon-title-info"><span className="customize-control-title slider-control-label">{label}</span></div>;
	}

	if (suffix) {

		let suffixOption = [];

		if ( typeof(suffix) === 'object' ) {
			suffix.forEach( item => {
				suffixOption.push(
					{ value: item, label: item }
				)
			});
		}

		suffixHtml = <SelectControl
			key={ stateSuffix }
			className={ classnames( 'ae-unit-picker__select', state === 0 ? 'picker-inactive' : 'picker-active' ) }
			value={ stateSuffix }
			onChange={ ( unit ) => {
				setStateSuffix( unit );
				props.control.settings.sizeUnit.set( unit );
			} }
			options={ suffixOption }
		/>
	}

	return <div className="aeon-slider-wrap">
		<div className="aeon-title-area">
			{labelHtml}
			<div className="aeon-slider-buttons">
				{ resetButton( defaultVal, defaultValUnit ) }
			</div>
		</div>
		<div className="aeon-slider-input-wrapper">
			{renderInputHtml()}
			{suffixHtml}
		</div>
	</div>;
};

SliderComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default React.memo( SliderComponent );
