import './style.scss';
import PropTypes from 'prop-types';
import {useState} from 'react';
import { Button } from '@wordpress/components';

const SelectorComponent = props => {
	const [propsValue, setPropsValue] = useState(props.control.setting.get());

	const onValueChange = ( value, device = '' ) => {
		let updateState = {
			...propsValue
        };
        if ( '' !== device ) {
            updateState[device] = value;
        } else {
            updateState = value;
        }

		props.control.setting.set(updateState);
		setPropsValue(updateState);
	};

	const renderInputHtml = ( device, active = '', resp = true ) => {
		const {
			choices,
			renderAs
		} = props.control.params;

		if ( ! choices ) {
			return;
		}

		if ( false === resp ) {
			let optionsHtml = Object.entries( choices ).map( ( [value, icon] ) => {
				if ( 'text' !== renderAs ) {
					var html = (
						<div className="aeon-alignment-inner-wrap active" key={ value }>
							<Button
								key={ value }
								onClick={ () => onValueChange( value ) }
								aria-pressed = { value === propsValue }
								isPrimary = { value === propsValue }
							>
								<span className="aeon-icon-set"
									dangerouslySetInnerHTML={ { __html: icon  } }
								></span>
							</Button>
						</div>
					);
				} else {
					var html = (
						<div className="aeon-alignment-inner-wrap active" key={ value }>
							<Button
								key={ value }
								onClick={ () => onValueChange( value ) }
								aria-pressed = { value === propsValue }
								isPrimary = { value === propsValue }
								label = { icon }
							>
								{ icon }
							</Button>
						</div>
					);
				}
				return html;
			} );
			return optionsHtml;
		}

		if ( 'text' !== renderAs ) {
			var optionsHtml = Object.entries( choices ).map( ( [value, icon] ) => {
				let html = (
					<div className={ `aeon-alignment-inner-wrap aeon-alignment-responsive ${device} ${active}` } key={ value } >
						<Button
							key={ value }
							onClick={ () => onValueChange( value, device ) }
							aria-pressed = { value === propsValue[device] }
							isPrimary = { value === propsValue[device] }
						>
							<span className="aeon-icon-set"
								dangerouslySetInnerHTML={ { __html: icon  } }
							></span>
						</Button>
					</div>
				);
				return html;
			} );
		} else {
			var optionsHtml = Object.entries( choices ).map( ( [value, icon] ) => {
				let html = (
					<div className={ `aeon-alignment-inner-wrap aeon-alignment-responsive ${device} ${active}` } key={ value } >
						<Button
							key={ value }
							onClick={ () => onValueChange( value, device ) }
							aria-pressed = { value === propsValue[device] }
							isPrimary = { value === propsValue[device] }
							label = { icon }
						>
							{ icon }

						</Button>
					</div>
				);
				return html;
			} );

		}

		return optionsHtml;
	};

	const {
		label,
		responsive
	} = props.control.params;
	let labelHtml = null;
	let responsiveHtml = null;
	let inputHtml = null;

	let responsiveFlag = ( false === responsive ) ? false : true;

	if (label) {
		labelHtml = <div className="aeon-title-info"><span className="customize-control-title">{label}</span></div>;

		if (responsiveFlag) {
			responsiveHtml = <ul key={'aeon-resp-devices'} className="aeon-device-controls">
				<li key={'desktop'} className="desktop active">
					<button type="button" className="preview-desktop" data-device="desktop">
						<i className="dashicons dashicons-desktop"></i>
					</button>
				</li>
				<li key={'tablet'} className="tablet">
					<button type="button" className="preview-tablet" data-device="tablet">
						<i className="dashicons dashicons-tablet"></i>
					</button>
				</li>
				<li key={'mobile'} className="mobile">
					<button type="button" className="preview-mobile" data-device="mobile">
						<i className="dashicons dashicons-smartphone"></i>
					</button>
				</li>
			</ul>;
		}
	}

	if (responsiveFlag) {
		inputHtml = <>
		<div className ="aeon-selector-responsive-wrap desktop">
			{renderInputHtml('desktop', 'active')}
		</div>
		<div className ="aeon-selector-responsive-wrap tablet">
			{renderInputHtml('tablet')}
		</div>
		<div className ="aeon-selector-responsive-wrap mobile">
			{renderInputHtml('mobile')}
		</div>
		</>;
	} else {
		inputHtml = <>
			{renderInputHtml('desktop', 'active', false)}
		</>;
	}

	return <div>
		<div key={'customizer-text'} className="aeon-title-area">
			{labelHtml}
			{responsiveHtml}
		</div>
		<div className="input-wrapper aeon-alignment-wrapper">
			{inputHtml}
		</div>
	</div>;

};

SelectorComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default React.memo( SelectorComponent );
