import './style.scss';

import {
	__,
} from '@wordpress/i18n';

import {
	Button,
	Tooltip,
	Popover,
	BaseControl,
	ColorPicker,
	TextControl,
} from '@wordpress/components';

import {
	useState,
	useEffect,
} from '@wordpress/element';

const AeonColorPickerControl = ( props ) => {
	const [ isOpen, setOpen ] = useState( false );
	const [ colorKey, setColorKey ] = useState( false );
	const [ isVarLock, setVarLock ] = useState( true );
	const [ isManualInput, setManualInput ] = useState( false );

	const {
		value,
		varNameValue,
		onChange,
		onVarChange,
		choices,
		tooltipPosition = 'top center',
		tooltipText = __( 'Choose Color', 'aeonwp' ),
		hideLabel = false,
	} = props;

	useEffect( () => {
		if ( ! value ) {
			setVarLock( false );
		} else {
			setVarLock( true );
		}
	}, [ value ] );

	useEffect( () => {
		if ( ! isManualInput ) {
			return;
		}

		const timeout = setTimeout( () => {
			setColorKey( value );

			const colorInput = document.querySelector( '.aeon-color-input-wrapper input' );

			if ( colorInput ) {
				colorInput.focus();
			}
		}, 350 );

		return () => {
			clearTimeout( timeout );
			setManualInput( false );
		};
	}, [ value ] );

	const toggleVisible = () => {
		setOpen( true );
	};

	const toggleClose = () => {
		setOpen( false );
		setVarLock( true );
	};

	const isHex = ( hex ) => {
		return /^([0-9A-F]{3}){1,2}$/i.test( hex );
	};

	const getPaletteValue = ( colorValue ) => {
		if ( String( colorValue ).startsWith( 'var(' ) ) {
			const variableName = colorValue.match( /\(([^)]+)\)/ );

			if ( variableName ) {
				const variableValue = getComputedStyle( document.documentElement ).getPropertyValue( variableName[ 1 ] );

				if ( variableValue ) {
					colorValue = variableValue;
				}
			}
		}

		return colorValue;
	};

	let tooltip = tooltipText;

	if ( choices.tooltip ) {
		tooltip = choices.tooltip;
	}

	const showReset = !! choices.showReset || 'undefined' === typeof choices.showReset;

	return (
		<div className="aeon-color-picker-area">
			<div className="components-circular-option-picker__option-wrapper">
				{ ! isOpen &&
					<Tooltip text={ tooltip } position={ tooltipPosition }>
						<Button
							aria-expanded={ isOpen }
							className="components-color-palette__item components-circular-option-picker__option"
							onClick={ toggleVisible }
							aria-label={ tooltip }
							style={ { color: value ? value : 'transparent' } }
						>
							<span className="components-color-palette__custom-color-gradient" />
						</Button>
					</Tooltip>
				}

				{ isOpen &&
					<Tooltip text={ tooltip } position={ tooltipPosition }>
						<Button
							aria-expanded={ isOpen }
							className="components-color-palette__item components-circular-option-picker__option"
							onClick={ toggleClose }
							aria-label={ tooltip }
							style={ { color: value ? value : 'transparent' } }
						>
							<span className="components-color-palette__custom-color-gradient" />
						</Button>
					</Tooltip>
				}
			</div>

			{ isOpen &&
				<Popover
					position="bottom center"
					className="aeon-component-color-picker"
					onClose={ toggleClose }
					focusOnMount="container"
				>
					<BaseControl
						key={ colorKey }
						label={ !! props.label && ! hideLabel ? props.label : '' }
						id="aeon-color-input-field"
						className="aeon-color-input-main-label"
					>
						<ColorPicker
							key={ colorKey }
							color={ getPaletteValue( value ) || '' }
							onChangeComplete={ ( color ) => {
								let colorString;

								if ( 'undefined' === typeof color.rgb || color.rgb.a === 1 ) {
									colorString = color.hex;
								} else {
									const { r, g, b, a } = color.rgb;
									colorString = `rgba(${ r }, ${ g }, ${ b }, ${ a })`;
								}

								onChange( colorString );
							} }
							disableAlpha={ ! choices.alpha }
						/>

						<div className="aeon-color-option-area">
							<div className="aeon-color-input-wrapper">
								<TextControl
									id="aeon-color-input-field"
									className="aeon-color-input"
									type={ 'text' }
									value={ value || '' }
									onChange={ ( color ) => {
										if ( ! color.startsWith( '#' ) && isHex( color ) ) {
											color = '#' + color;
										}

										onChange( color );
										setManualInput( true );
									} }
								/>

								{ !! showReset &&
									<Button
										isSmall
										isSecondary
										className="components-color-clear-color"
										onClick={ () => {
											const defaultValue = ( props.defaultValue ) ? props.defaultValue : '';

											wp.customize.control( props.customizerSetting.id ).setting.set( defaultValue );

											setTimeout( function() {
												document.querySelector( '.aeon-color-input-wrapper input' ).focus();
											}, 10 );
										} }
									>
										{ __( 'Default', 'aeonwp' ) }
									</Button>
								}
							</div>
						</div>
					</BaseControl>
				</Popover>
			}
		</div>
	);
};

export default AeonColorPickerControl;
