import './style.scss';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import {SelectControl, RangeControl, Dashicon} from '@wordpress/components';
import {useEffect, useState} from 'react';

const RespSliderComponent = props => {
	let prop_value = props.control.settings.size ? props.control.settings.size.get() : '',
		suffix_value = props.control.settings.sizeUnit ? props.control.settings.sizeUnit.get() : props.control.params.defaultUnit;

	const [state, setState] = useState( prop_value );
	const [stateSuffix, setStateSuffix] = useState( suffix_value );

	useEffect( () => {
		// If settings are changed externally.
		if ( state !== prop_value ) {
			setState( prop_value );
		}
		if ( stateSuffix !== suffix_value ) {
			setStateSuffix( suffix_value );
		}
	}, [props]);

	const updateValues = (device, newVal) => {
		let updateState = {...state};
		updateState[device] = newVal;
		props.control.settings.size.set(updateState);
		setState(updateState);
	};

	const {
		label,
		suffix
	} = props.control.params;
	
	const resetButton = ( defaultVal, defaultValUnit ) => {
		return (
			<div className="aeon-slider-reset-wrap">
				<button
					className="aeon-reset"
					disabled={ JSON.stringify(state) === JSON.stringify(defaultVal) }
					onClick={ e => {
						e.preventDefault();
						props.control.settings.size.set(defaultVal);
						setState(defaultVal);

						if (suffix) {
							props.control.settings.sizeUnit.set(defaultValUnit);
							setStateSuffix(defaultValUnit);
						}
					} }
				>
				<Dashicon icon='image-rotate'/>
				</button>
			</div>
		);
	};

	const renderInputHtml = (device, active = '') => {
		const {
			input_attrs,
		} = props.control.params;

		let defaultVal = props.control.params.default[device];

		let maxVal = 100,
			stepVal = 1;

		if (suffix) {
			if ( 'px' === stateSuffix ) {
				maxVal = 200;
				stepVal = 1;
			} else if ( '%' === stateSuffix ) {
				maxVal = 100;
				stepVal = 1;
			} else if ( 'em' === stateSuffix || 'rem' === stateSuffix || 'vh' === stateSuffix || 'vw' === stateSuffix ) {
				maxVal = 20;
				stepVal = .1;
			}
		}

		const defaults = { min: 0, max: maxVal, step: stepVal };
		const controlProps = {
			...defaults,
			...( input_attrs || {} ),
		};
		const { min, max, step } = controlProps;

		let savedValue = ( state[device] || 0 === state[device] ) ? parseFloat( state[device] ) : '';

		if ( 1 === step ) {
			savedValue = ( state[device] || 0 === state[device] ) ? parseInt( state[device] ) : '';
		}

		return <div className={`input-field-wrapper ${device} ${active}`}>
			<RangeControl
				resetFallbackValue={ defaultVal }
				value={ savedValue }
				min={ min }
				max={ max }
				step={ step }
				onChange={ ( newVal ) => { updateValues( device, newVal ) } }
			/>
		</div>;
	};

	let labelHtml = null,
		responsiveHtml = null,
		suffixHtml = null,
		inputHtml = null,
		defaultVal = props.control.params.default,
		defaultValUnit = props.control.params.defaultUnit;

	if (label) {
		labelHtml = <div className="aeon-title-info"><span className="customize-control-title slider-control-label">{label}</span></div>;
		responsiveHtml = <ul key={'aeon-slider-devices'} className="aeon-device-controls">
			<li key={'desktop'} className="desktop active">
				<button type="button" className="preview-desktop" data-device="desktop">
					<i className="dashicons dashicons-desktop"></i>
				</button>
			</li>
			<li key={'tablet'} className="tablet">
				<button type="button" className="preview-tablet" data-device="tablet">
					<i className="dashicons dashicons-tablet"></i>
				</button>
			</li>
			<li key={'mobile'} className="mobile">
				<button type="button" className="preview-mobile" data-device="mobile">
					<i className="dashicons dashicons-smartphone"></i>
				</button>
			</li>
		</ul>;
	}

	if (suffix) {

		let suffixOption = [];

		if ( typeof(suffix) === 'object' ) {
			suffix.forEach( item => {
				suffixOption.push(
					{ value: item, label: item }
				)
			});
		}

		suffixHtml = <SelectControl
			key={ stateSuffix }
			className={ classnames( 'ae-unit-picker__select', state['desktop'] === 0 ? 'picker-inactive' : 'picker-active' ) }
			value={ stateSuffix }
			onChange={ ( unit ) => {
				setStateSuffix( unit );
				props.control.settings.sizeUnit.set( unit );
			} }
			options={ suffixOption }
		/>
	}

	inputHtml = <>
		{renderInputHtml('desktop', 'active')}
		{renderInputHtml('tablet')}
		{renderInputHtml('mobile')}
	</>;

	return <>
		<div key={'customizer-text'} className="aeon-title-area">
			{labelHtml}
			<div className="aeon-slider-buttons">
				{responsiveHtml}
				{ resetButton( defaultVal, defaultValUnit ) }
			</div>
		</div>
		<div className="aeon-slider-inputs-wrapper">
			{inputHtml}
			{suffixHtml}
		</div>
	</>;
};

RespSliderComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default RespSliderComponent;
