import './style.scss';
import PropTypes from 'prop-types';
import {__} from '@wordpress/i18n';
import {useEffect, useState} from 'react';

const SpacingComponent = props => {

	let value = props.control.setting.get()
	value = (undefined === value || '' === value) ? props.control.params.value : value;
	const [state, setState] = useState(value);

	useEffect( () => {
		// If settings are changed externally.
		if( state !== value ) {
			setState(value);
		}
	}, [props]);

	const onConnectedClick = () => {
		let parent = event.target.parentElement.parentElement;
		let inputs = parent.querySelectorAll('.aeon-spacing-input');

		for (let i = 0; i < inputs.length; i++) {
			inputs[i].classList.remove('connected');
		}

		event.target.parentElement.classList.remove('disconnected');
	};

	const onDisconnectedClick = () => {
		let parent = event.target.parentElement.parentElement;
		let inputs = parent.querySelectorAll('.aeon-spacing-input');

		for (let i = 0; i < inputs.length; i++) {
			inputs[i].classList.add('connected');
		}

		event.target.parentElement.classList.add('disconnected');
	};

	const onSpacingChange = (device, choiceID) => {
		const {
			choices
		} = props.control.params;
		let updateState = {
			...state
		};
		let deviceUpdateState = {
			...updateState[device]
		};

		if (!event.target.classList.contains('connected')) {
			deviceUpdateState[choiceID] = event.target.value;
		} else {
			for (let choiceID in choices) {
				deviceUpdateState[choiceID] = event.target.value;
			}
		}

		updateState[device] = deviceUpdateState;
		props.control.setting.set(updateState);
		setState(updateState);
	};

	const onUnitChange = (device, unitKey = '') => {
		let updateState = {
			...state
		};
		updateState[`${device}-unit`] = unitKey;
		props.control.setting.set(updateState);
		setState(updateState);
	};

	const renderResponsiveInput = (device) => {
		return <input key={device} type='hidden' onChange={() => onUnitChange(device, '')}
					  className={`aeon-spacing-unit-input aeon-spacing-${device}-unit`} data-device={`${device}`}
					  value={state[`${device}-unit`]}></input>;
	};

	const renderInputHtml = (device, active = '') => {
		const {
			choices,
			inputAttrs,
			connected
		} = props.control.params;

		let connectedClass = ( false === connected ) ? '' : 'connected';
		let disconnectedClass = ( false === connected ) ? '' : 'disconnected';
		let itemLinkDesc = __('Link Values Together', 'aeonwp');
		let linkHtml = null;
		let htmlChoices = null;

		if( choices ) {
			htmlChoices = Object.keys(choices).map(choiceID => {
				let html = <li key={choiceID} {...inputAttrs} className='aeon-spacing-input-item'>
					<input type='number' className={`aeon-spacing-input aeon-spacing-${device} ${connectedClass}`} data-id={choiceID}
						   value={state[device][choiceID]} onChange={() => onSpacingChange(device, choiceID)}/>
					<span className="aeon-spacing-title">{choices[choiceID]}</span>
				</li>;
				return html;
			});
		}

		linkHtml = <li key={'connect-disconnect' + device} className={ `aeon-spacing-input-item-link ${disconnectedClass}` }>
				<span key={'connect' + device}
						className="dashicons dashicons-admin-links aeon-spacing-connected wp-ui-highlight"
						onClick={() => {
							onConnectedClick();
						}} title={itemLinkDesc}></span>
			<span key={'disconnect' + device} className="dashicons dashicons-editor-unlink aeon-spacing-disconnected"
					onClick={() => {
						onDisconnectedClick();
					}} title={itemLinkDesc}></span>
		</li>;

		return <ul key={device} className={`aeon-spacing-wrapper ${device} ${active}`}>
			{htmlChoices}
			{linkHtml}
		</ul>;
	};

	const {
		label,
		description
	} = props.control.params;
	let htmlLabel = null;
	let htmlDescription = null;
	let inputHtml = null;
	let responsiveHtml = null;

	responsiveHtml = <>
		<div className="aeon-spacing-unit">
			{renderResponsiveInput('desktop')}
			{renderResponsiveInput('tablet')}
			{renderResponsiveInput('mobile')}
		</div>
		<ul key={'aeon-spacing-devices'} className="aeon-device-controls">
			<li key={'desktop'} className="desktop active">
				<button type="button" className="preview-desktop" data-device="desktop">
					<i className="dashicons dashicons-desktop"></i>
				</button>
			</li>
			<li key={'tablet'} className="tablet">
				<button type="button" className="preview-tablet" data-device="tablet">
					<i className="dashicons dashicons-tablet"></i>
				</button>
			</li>
			<li key={'mobile'} className="mobile">
				<button type="button" className="preview-mobile" data-device="mobile">
					<i className="dashicons dashicons-smartphone"></i>
				</button>
			</li>
		</ul>
	</>;

	if (label) {
		htmlLabel = <div className="aeon-title-area">
			<div className="aeon-title-info">
				<span className="customize-control-title">{label}</span>
			</div>
			<div className="aeon-spacing-responsive-wrap">
				{responsiveHtml}
			</div>
		</div>
	}

	if (description) {
		htmlDescription = <span className="description customize-control-description">{description}</span>;
	}

	inputHtml = <>
		{renderInputHtml('desktop', 'active')}
		{renderInputHtml('tablet')}
		{renderInputHtml('mobile')}
	</>;

	return <div key={'aeon-spacing-responsive'} className='aeon-spacing-responsive'>
		{htmlLabel}
		{htmlDescription}
		<div className="aeon-spacing-inputs-wrapper">
			<div className="input-wrapper">
				{inputHtml}
			</div>
		</div>
	</div>;

};

SpacingComponent.propTypes = {
	control: PropTypes.object.isRequired
};

export default SpacingComponent;