import {
	render,
} from '@wordpress/element';

import {
	SlotFillProvider,
	Popover,
} from '@wordpress/components';

const AeonColorControl = {
	extend: ( FormComponent, controlExtend = {} ) => wp.customize.aeControl.extend( Object.assign( {}, {
		/**
		 * After control has been first rendered, start re-rendering when setting changes.
		 *
		 * React is able to be used here instead of the wp.customize.Element abstraction.
		 *
		 * @return {void}
		 */
		ready: function ready() {
			const control = this;

			// Re-render control when setting changes.
			control.setting.bind( () => {
				control.renderContent();
			} );
		},

		/**
		 * Render the control into the DOM.
		 *
		 * This is called from the Control#embed() method in the parent class.
		 *
		 * @return {void}
		 */
		renderContent: function renderContent() {
			const control = this;
			const value = control.setting.get();

			const form = <SlotFillProvider>
				<FormComponent
					{ ...control.params }
					value={ value }
					setNotificationContainer={ control.setNotificationContainer }
					customizerSetting={ control.setting }
					control={ control }
					choices={ control.params.choices }
					default={ control.params.defaultValue }
				/>
				<Popover.Slot />
			</SlotFillProvider>;

			const wrapper = control.getWrapper();

			render(
				form,
				wrapper
			);
		},
	}, controlExtend ) ),
};

export default AeonColorControl;
