import { useState , useEffect } from 'react';

const { __ } = wp.i18n;
const {Dashicon, TextControl, Button } = wp.components;

const ItemComponent = props => {

	const [state, setState] = useState({
		open: false,
	});

	const urlLabel = 'phone' === props.item.id ? __('Number', 'aeonwp') : __('URL', 'aeonwp');

	return <div className={`aeon-sorter-item ${props.item.enabled ? '' : 'is-disabled'}`} data-id={props.item.id} key={props.item.id}>
		<div className="aeon-sorter-item-panel-header" onClick={() => {
			setState((prevState => ({
				...prevState,
				open: state.open ? false : true
			})))
		}}>
			<Button className={`aeon-sorter-item-expand ${props.item.enabled ? 'item-is-visible' : 'item-is-hidden'}`}
					onClick={e => {
						e.stopPropagation();
						props.toggleEnabled(props.item.enabled ? false : true, props.index);
					}}>
				<Dashicon icon="visibility"/>
			</Button>
			<span className="aeon-sorter-title">
				{undefined !== props.item.label && '' !== props.item.label ? props.item.label : __('Social Item', 'aeonwp')}
			</span>
			<Button className="aeon-sorter-item-remove" isDestructive onClick={() => {props.removeItem(props.index);}}>
				<Dashicon icon="no-alt"/>
			</Button>
		</div>
		{ state.open && <div className="aeon-sorter-item-panel-content">
			<TextControl label={`${urlLabel}`} value={props.item.url ? props.item.url : ''} onChange={value => {
				props.onChangeURL(value, props.index);
			}}/>
		</div>}
	</div>;
};
export default ItemComponent;
